import React,{useState} from 'react'
import {NavLink} from 'react-router-dom'
import {ToastContainer, toast} from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const Signup = () => {
  const [logdata,setData]=useState({
    fname:"",
    email:"",
    mobile:"",
    password:"",
    cpassword:""
  });
  //console.log(logdata);
  const adddata=(e)=>{
    const {name,value} = e.target;
    setData(()=>{
      return {
        ...logdata,
        [name]:value
      }
    })
  };
  const senddata=async(e)=>{
  e.preventDefault();
  const { fname, email, mobile, password, cpassword } = logdata;
  
      const res = await fetch("register", {
          method: "POST",
          headers: {
              "Content-Type": "application/json"
          },
          body: JSON.stringify({
              fname, email, mobile, password, cpassword
          })
      });

      const data = await res.json();
      // console.log(data);  
       if(res.status===422||!data)
       {toast.warn("invalid details", {
        position: "top-center",
      })
       }
       else{
      // alert("data successfully added")
       toast.success("data successfully added", {
        position: "top-center",
       })
       setData({...adddata,fname:"",email:"",mobile:"",password:"",cpassword:""});
       }
    };

  return (
    <>
      <section>
        <div className='sign_container'>
          <div className='sign_header'>
            <img src="https://upload.wikimedia.org/wikipedia/commons/thumb/a/a9/Amazon_logo.svg/1200px-Amazon_logo.svg.png" alt="amazonlogo"/>
           
          </div>
          <div className='sign_form'>
            <form method='POST'>
              <h1>Create Account</h1>
             
              <div className='form_data'>
                <label htmlFor='fname'>Your Name &nbsp;</label>
                <input type="text" 
                onChange={adddata}
                value={logdata.fname}
                name="fname" id="fname"/>
              </div>
              <div className='form_data'>
                <label htmlFor='email'>Email&nbsp;</label>
                <input type="text" 
                onChange={adddata}
                value={logdata.email}
                name="email" id="email"/>
              </div>
              <div className='form_data'>
                <label htmlFor='number'>Mobile&nbsp;</label>
                <input type="text" 
                onChange={adddata}
                value={logdata.mobile}
                name="mobile" id="mobile"/>
              </div>
             
              <div className='form_data'>
                <label htmlFor="password">Password&nbsp;</label>
                <input type="password" 
                onChange={adddata}
                value={logdata.password}
                name="password" placeholder='At least 6 char' id="password"/>
              </div>
              
              <div className='form_data'>
                <label htmlFor="cpassword">Password Again&nbsp;</label>
                <input type="password" 
                onChange={adddata}
                value={logdata.cpassword}
                name="cpassword" id="cpassword"/>
              </div>
              <button className='signin_btn' onClick={senddata}>Continue</button>
              <div className='signin_info'>
                <p>Already have an account?<NavLink to="/login">Signin</NavLink></p>
                
              </div>
            </form>
          </div>
          <ToastContainer/>
        </div>
      </section>
   </>

  )
}

export default Signup