import React from 'react'
import Carousel from 'react-material-ui-carousel'
import './Banner.css'
const data = [
   "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQEbEawIAieEOVqWIiPXYA16FaEnQdv_maCAQ&usqp=CAU",
   "https://www.financialexpress.com/wp-content/uploads/2022/08/amazon.jpg?w=350",
   "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSZavdDkPzpkOeZQ3moU7StZ0XAObAUUXhkmQ&usqp=CAU",
   "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQLaU5LtQ5JdwZR4mZMywUYphphFlWGP_BI3A&usqp=CAU"
]
const Banner = () => {
  return (
   <Carousel 
  //  from npm module website
   className='carasoul'
   indicators={false}
  //  autoPlay={true}
  //  animation='slide'
  //  indicators={false}
  //  navButtonsAlwaysVisible={true}
  //  cycleNavigation={true}
  //  navButtonsProps={{
  //   style:{
  //     backgroundColor:"#ffff",
  //     color:"#494949",
  //     borderRadius:0,
  //     marginTop:-22,
  //     height:"104px"
      
  //   }
  //  }}
   >
    {
      data.map((imag,i)=>{
        return(
          <>
          <img src={imag} alt="" className='banner_img'/>
          </>
        )
      })
    }

   </Carousel>
  )
}

export default Banner;
