import { React, useContext, useEffect, useState } from 'react';
import './navbar.css';
import SearchIcon from '@mui/icons-material/Search';
import Badge from '@mui/material/Badge';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import Avatar from '@mui/material/Avatar';
import { NavLink } from 'react-router-dom';
import { useNavigate } from 'react-router-dom';
import { LoginContext } from '../Context/ContextProvider';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import Drawer from '@mui/material/Drawer';
import RightHeader from "./Rightheader";
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import LogoutIcon from '@mui/icons-material/Logout';
import {ToastContainer, toast} from 'react-toastify';
import { useSelector } from 'react-redux';


const Navbar = () => {
  const navigate = useNavigate("");
  

  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  const [dropen, setdropen] = useState("");

  const getdetailvaliduser = async () => {
    const res = await fetch("/validuser", {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json"
      },
      credentials: "include"
    });
    const data = await res.json();
    console.log(data);

    if (res.status !== 201) {
      console.log("error");
    } else {
      console.log("data valid");
      setAccount(data);
    }
  };

  const logoutuser = async () => {
    const res2 = await fetch("/logout", {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json"
      },
      credentials: "include"
    });
    const data2 = await res2.json();
    console.log(data2);

    if (res2.status !== 201) {
      console.log("error");
    } else {
      console.log("data valid");
      toast.success("user logout",{
        position:"top-center",
      })
      //alert("logout") 
      navigate('/');
      setAccount(false);
      
    }
  };


  const handleopen = () => {
    setdropen(true);
  }

  const handledrclose = () => {
    setdropen(false)
  }
  const { account, setAccount } = useContext(LoginContext);
  //console.log(account);
  const[text,setText]=useState("");
  console.log(text);
  
  const[liopen,setliopen]=useState(true);

  const {products}=useSelector(state => state.getproductsdata);

  const getText=(items)=>{
    setText(items);
    setliopen(false);
  }

  useEffect(() => {
    getdetailvaliduser()
  }, []);
 
  return (
    <header>
      <nav className='navbar'>
        <div className='left'>
          <IconButton className='hamburger' onClick={handleopen}>
            <MenuIcon style={{ color: "#fff" }} />
          </IconButton>
          <Drawer open={dropen} onClose={handledrclose}>
            <RightHeader Logclose={handledrclose} logoutuser={logoutuser}/>
          </Drawer>

          <div className='image'>
            <NavLink to="/"><img src="https://i0.wp.com/www.dafontfree.co/wp-content/uploads/2021/11/Amazon-Logo-Font-1-scaled.jpg?resize=2560%2C1578" alt="not found" />
            </NavLink>
          </div>

          <div className='search'>
            <input type="search" 
            onChange={(e)=>getText(e.target.value)}
             placeholder="Search" />
          </div>
          <div className='search_icon'>
            <SearchIcon />
          </div>

         {/*Search Filter*/}
         {
          text&&
          <List className='extrasearch' hidden = {liopen}>
           {
            products.filter(product=>product.title.longTitle.toLowerCase().includes(text.toLowerCase())).map(product=>(
              <ListItem>
                <NavLink to={`/getproductsone/${product.id}`} onClick={()=>setliopen(true)}>
                {product.title.longTitle}
              </NavLink>
              </ListItem>
            ))
           } 
          </List>
         }
        </div>
        <div className='right'>
          <div className='nav_btn'>
            <NavLink to="/login" id='sign'>signin</NavLink>
          </div>
          <h1 title="cart">
            {
              account? <NavLink to="/buynow"><div className='cart_btn'>
                <Badge color="primary" badgeContent={account.carts.length}>
                  <ShoppingCartIcon id="icon" />
                </Badge></div>
                 </NavLink> : <NavLink to="/login">
              <div className='cart_btn'>
                <Badge color="primary" badgeContent={0}>
                  <ShoppingCartIcon id="icon" />
                </Badge>
                </div>
              </NavLink>
            }
           <ToastContainer/>
            {/* <p id="cart">Cart</p> */}
          </h1>
          <Avatar className="avtar" alt="Remy Sharp"
            src="data:image/jpeg;base64,"
            sx={{ width: 26, height: 26 }}
            id="demo-positioned-button"
            aria-controls={open ? 'demo-positioned-menu' : undefined}
            aria-haspopup="true"
            aria-expanded={open ? 'true' : undefined}
            onClick={handleClick} />
          {/* {  
            
                        account?<Avatar className="avtar">{account.carts.fname[0].toUpperCase()}</Avatar> :
                            <Avatar className="avtar"/>
                    } */}

          <Menu
            id="demo-positioned-menu"
            aria-labelledby="demo-positioned-button"
            anchorEl={anchorEl}
            open={open}
            onClose={handleClose}
            anchorOrigin={{
              vertical: 'top',
              horizontal: 'left',
            }}
            transformOrigin={{
              vertical: 'top',
              horizontal: 'left',
            }}
          >
            <MenuItem onClick={handleClose}>My account</MenuItem>
            {
              account?<div className='cli' onClick={logoutuser}><MenuItem onClick={handleClose} ><LogoutIcon style={{fontSize:16,marginRight:3}}/>Logout</MenuItem></div>:""
            }
            
          </Menu>
        </div>
      </nav>
    </header>
  )
}

export default Navbar;
