import React, { useState,useContext } from 'react'
import { NavLink } from 'react-router-dom'
import {ToastContainer, toast} from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { LoginContext } from '../Context/ContextProvider';
import "./sign.css"

const Signin = () => {
  // const [logdata,setData] = useState(0);
  const [logdata, setData] = useState({
    email: "",
    password: ""
  });
  //console.log(logdata);

  const {account,setAccount} = useContext(LoginContext);


  const adddata = (e) => {
    const { name, value } = e.target;

    setData(() => {
      return {
        ...logdata,
        [name]: value
      }
    })
  }
  const senddata = async (e) => {
    e.preventDefault();
    const { email, password } = logdata;
    const res = await fetch("/login", {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
       
      },
      body: JSON.stringify({
        email, password
      })
    });
    const data = await res.json();
    console.log(data);
    if (res.status === 400 || !data) {
      console.log("invalid details");
      toast.warn("invalid details", {
        position: "top-center",
      })
    }
    else {
    console.log("datavalid");
      setAccount(data);
      toast.success("User valid", {
        position: "top-center",
      })
      setData({ ...logdata, email: "", password: "" });
    }
  }
  return (
    <>
      <section>
        <div className='sign_container'>
          <div className='sign_header'>
            <img src="https://upload.wikimedia.org/wikipedia/commons/thumb/a/a9/Amazon_logo.svg/1200px-Amazon_logo.svg.png" alt="amazonlogo" />
          </div>
          <div className='sign_form'>
            <form method='POST'>
              <h1>Sign-In</h1>
              <div className='form_data'>
                <label htmlFor='email'>Email &nbsp; </label>
                <input type="text"
                  onChange={adddata}
                  value={logdata.email}
                  name="email" id="email" />
              </div>
              <div className='form_data'>
                <label htmlFor="password">Password &nbsp;</label>
                <input type="password"
                  onChange={adddata}
                  value={logdata.password}
                  name="password" placeholder='At least 6 char' id="password" />
              </div>
              <button className='signin_btn' onClick={senddata}>Continue</button>
            </form>
          </div>
          <div className='create_accountinfo'>
            <p>new To Amazon</p>
            <NavLink to="/register"><button>Create new Amazon account</button></NavLink>
          </div>
          <ToastContainer/>
        </div>
      </section>
    </>
  )
}

export default Signin
