import React, { useContext, useEffect, useState } from 'react'
import { Divider } from "@mui/material"
import { useNavigate, useParams } from "react-router";
import "./cart.css";
import { LoginContext } from '../Context/ContextProvider';
import CircularProgress from '@mui/material/CircularProgress';


const Cart = () => {

  const { id } = useParams("");
  //console.log(id);

  const history = useNavigate("");

  const { account, setAccount } = useContext(LoginContext);
  const [inddata, setInddata] = useState("");
  console.log(inddata);

  const getinddata = async () => {
    const res = await fetch(`/getproductsone/${id}`, {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json"
      },
      credentials: "include"
    });
    const data = await res.json();
    //console.log(data);
    if (res.status !== 201) {
      console.log("no data available");
    } else {
      console.log("getdata");
      setInddata(data);
    }
  }

  useEffect(() => {     //Used to see chenges
    setTimeout(getinddata, 1000)

  }, [id]);

  //add cart function
  const addtocart = async (id) => {
    console.log(id);
    const checkres = await fetch(`/addcart/${id}`, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json"

      },
      body: JSON.stringify({
        inddata
      }),
      credentials: "include"
    });

    const data1 = await checkres.json();
    console.log(data1);
    // const a=checkres;
    // console.log(a);
    if (checkres.status === 401 || !data1) {
      console.log("user invalid");
      alert("user invalid");
    }
    else {
      // alert("data added in your cart");
      history("/buynow")
      setAccount(data1);
    }

  }
  return <div className='cart_section'>
    {inddata && Object.keys(inddata).length &&
      <div className='cart_container'>
        <div className='left_cart'>
          <img src={inddata.url} alt="cart_img" />
          <div className='cart_btn'>
            <button className='cart_btn1' onClick={() => addtocart(inddata.id)}>Add to Cart</button>
            <button className='cart_btn2'>Buy Now</button>
          </div>
        </div>

        <div className='right_cart'>
          <h3>{inddata.title.shortTitle}</h3>
          <h4>{inddata.title.longTitle}</h4>
          <Divider />
          <p className="mrp">M.R.P. : ₹{inddata.price.mrp}</p>
          <p>{inddata.tagline}<span style={{ color: "#B12704" }}> ₹{inddata.price.cost}</span></p>
          <p>Your Save:<span style={{ color: "#B12704" }}>₹{inddata.price.mrp - inddata.price.cost}</span></p>

          <div className="discount_box">
            <h5 >Discount : <span style={{ color: "#111" }}>{inddata.discount}</span> </h5>
            <h4>FREE Delivery : <span style={{ color: "#111", fontWeight: "600" }}>Oct 8 - 21</span> Details</h4>
            <p style={{ color: "#111" }}>Fastest delivery: <span style={{ color: "#111", fontWeight: "600" }}> Tomorrow 11AM</span></p>
          </div>
          <p className="description">About the Iteam : <span style={{ color: "#565959", fontSize: "14px", fontWeight: "500", letterSpacing: "0.4px" }}>{inddata.description}</span></p>
        </div>
      </div>
    }
    {!inddata ? <div className='circle'>
      <CircularProgress />
      <h2>Loading...</h2>
    </div> : ""}
  </div>
};
export default Cart;
